<template>
  <div class="main">
    <b-carousel
      id="carousel-1"
      :interval="4000"
      controls
      indicators
      img-width="200"
      img-height="200"
      background="gray"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="mb-4 mt-4"
    >
      <b-carousel-slide
        v-for="(image1,index) in images" :key="index">
        <template v-slot:img>
          <img width="200"
               height="300"
               background-color="red"
               :src=image1.link
               class="d-block w-100"
          >
        </template>
      </b-carousel-slide>
    </b-carousel>
    <b-container>
      <pre>{{categories}}</pre>
      <div class="mx-auto text-center"><h1>Categories</h1></div>
      <b-row>
        <b-col v-for="category in categories" :key="category.name" sm="4" cols="6" lg="2" md="3" class="my-3">
          <category-box :category="category"></category-box>
        </b-col>
      </b-row>
      <div style="border: 0px solid black; background-color: white;">
        <div class="mx-auto text-center"><h1>Products</h1></div>
        <b-row cols-lg="5">
          <b-col v-for="(product,idx) in relatedProducts" :key="idx">
            <product-box :product="product"></product-box>
          </b-col>
        </b-row>
      </div>
      <b-container fluid class="d-flex justify-content-center">
        <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
          <b-button :disabled="busy" variant="success" v-if="!categoryLoadingDone" @click="loadCategories">
            Load More
          </b-button>
        </b-overlay>
      </b-container>
    </b-container>
  </div>
</template>

<script>
import CategoryBox from '../../components/Boxes/CategoryBox'
import loader from '../../mixins/loader'
import ProductBox from '../../components/Boxes/ProductBox'
export default {
  name: 'AllCategories',
  data () {
    return {
      categories: [],
      categoryLoadingDone: false,
      busy: false,
      sliding: false
    }
  },
  computed: {
    relatedProducts () {
      const limit = 10
      return this.categories.filter(category => category.products && category.products.length).map(category => category.products[0]).slice(0, limit)
    },
    images () {
      const limit = 6
      return this.categories.filter(category => category.images && category.images.length).map(category => category.images[0]).slice(0, limit)
    }
  },
  components: {
    CategoryBox,
    ProductBox
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadCategories () {
      this.busy = true
      const offset = this.categories.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/categories/all', { offset, limit }, 'categories')
        .then(response => {
          this.busy = false
          this.categories = this.categories.concat(response.data.categories)
          if (response.data.categories.length < limit) {
            this.categoryLoadingDone = true
          }
        })
    }
  },
  created () {
    this.loadCategories()
  }
}
</script>

<style scoped>
.main {
  background-color: rgb(221, 221, 221);
}
</style>
