<template>
  <router-link
    id="holder"
    tag="div"
    :to="{ name: 'CertainSubCategory', query: { 'cat-name': category, 'subcat-name': subCategory.name } }"
  >
    <b-card
      :class="{ shadow }" @mouseenter="shadow = true" @mouseleave="shadow = false"
      :img-src="imgSrc" img-width="150px" img-height="150px" img-alt="product.name" img-top>
      <b-card-text style="padding: 0 5%;">
        <div class="text-truncate">
          {{subCategory.name}}
        </div>
      </b-card-text>
    </b-card>
  </router-link>
</template>

<script>
export default {
  name: 'SubCategoryBox',
  data () {
    return {
      shadow: false
    }
  },
  props: [
    'category',
    'subCategory'
  ],
  computed: {
    imgSrc () {
      if (this.subCategory.images) {
        return this.subCategory.images[0].link
      } else if (this.subCategory.image) {
        return this.subCategory.image.link
      }
      return ''
    }
  },
  methods: {
    onHidden () {
      this.$refs.button.focus()
    }
  }
}
</script>

<style scoped>
  #holder {
    cursor: pointer;
    text-align: center;
  }
#imageId {
  height: auto;
  width: 90%;
  max-width: 150px;
  max-height: 150px;
}
</style>
