<template>
  <b-container :fluid="true" id="main" class="bv-example-row">
    <b-overlay :show="!productLoaded" spinner-large
        spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
      <b-carousel
        id="carousel-1"
        :interval="3000"
        controls
        indicators
        img-width="200"
        img-height="200"
        background="gray"
        style="text-shadow: 1px 1px 2px #333;"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
        class="mb-4"
      >
        <b-carousel-slide
          v-for="(image,index) in category.images" :key="index">
          <template v-slot:img>
            <img width="200"
              height="300"
              background-color="red"
              :src=image.link
              class="d-block w-100"
            >
          </template>
        </b-carousel-slide>
      </b-carousel>
      <b-row class="d-block d-md-none">
        <side-filter
          :route-name="$route.name"
          :brands="brands"
          :properties="spec.properties"
          :init-query="$route.query"
        ></side-filter>
      </b-row>
      <b-row class="">
        <b-col md="2" class="d-none d-md-block" style="padding: 0;">
          <side-filter
            :route-name="$route.name"
            :brands="brands"
            :properties="spec.properties"
            :init-query="$route.query"
          ></side-filter>
        </b-col>

        <b-col>
          <b-row class="mt-4 d-none d-md-block">
              <h1 style="text-align: center;">{{this.$route.query['cat-name']}}</h1>
            <hr><hr>
          </b-row>
          <h2 style="text-align: center;">Sub Categories</h2>
          <b-row>
            <b-col v-for="subCategory in subCategories" sm="4" cols="6" lg="2" md="3" :key="subCategory.name" class="my-3 padding-class col-lg-2 col-md-3 col-sm-4 col-xs-6">
              <sub-category-box :category="category.name" :sub-category="subCategory"></sub-category-box>
            </b-col>
          </b-row>
          <b-container fluid class="d-flex justify-content-center">
            <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
              <b-button :disabled="busy" variant="success" v-if="!subCategoryLoadingDone" @click="loadSubCategories">
                Load More
              </b-button>
            </b-overlay>
          </b-container>
          <hr>
          <h2 style="text-align: center;">Products</h2>
          <b-row>
            <b-col v-for="product in products" sm="4" cols="6" lg="2" md="3" :key="product.id" class="my-3 padding-class">
              <product-box :product="product"></product-box>
            </b-col>
          </b-row>
          <b-container fluid class="d-flex justify-content-center">
            <b-overlay :show="busy1" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
              <b-button :disabled="busy1" variant="success" v-if="!productLoadingDone" @click="loadProducts">
                Load More
              </b-button>
            </b-overlay>
          </b-container>
          <hr>
        </b-col>
      </b-row>
    </b-overlay>
  </b-container>
</template>

<script>
import SubCategoryBox from '../../components/Boxes/SubCategoryBox'
import ProductBox from '../../components/Boxes/ProductBox'
import SideFilter from '../../components/SideFilter'
import loader from '../../mixins/loader'

export default {
  name: 'CertainCategory',
  data () {
    return {
      category: {},
      images: [],
      spec: {
        properties: []
      },
      sliding: false,
      brands: [],
      relatedBrands: [],
      subCategories: [],
      subCategoryLoadingDone: true,
      products: [],
      productLoadingDone: true,
      dropText: 'popularity',
      busy: false,
      busy1: false,
      productLoaded: false
    }
  },
  computed: {
    imgSrc () {
      return this.category.image ? this.category.image.link : ''
    }
  },
  components: {
    SubCategoryBox,
    ProductBox,
    SideFilter
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadCategory () {
      const categoryName = this.$route.query['cat-name']
      this.loader('/category', { name: categoryName }, `category ${categoryName}`)
        .then(response => {
          this.category = response.data.category
          this.spec = response.data.spec
          this.brands = response.data.brands
          // if (response.data.category.images) {
          //   this.images = response.data.category.images
          // } else if (response.data.category.image) {
          //   this.images = response.data.category.image
          // }
          // this.images = response.data.category.images
        })
    },
    loadSubCategories () {
      const categoryName = this.$route.query['cat-name']
      if (!this.subCategoryLoadingDone) this.busy = true
      const offset = this.subCategories.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/sub-categories/all',
        { 'cat-name': categoryName, offset, limit },
        `sub-categories under ${categoryName}`)
        .then(response => {
          this.busy = false
          this.subCategories = this.subCategories.concat(response.data.subCategories)
          this.subCategoryLoadingDone = response.data.subCategories.length < limit
        })
    },
    setProducts (products, limit) {
      this.products = this.products.concat(products)
      this.productLoadingDone = products.length < limit
    },
    loadProducts () {
      const categoryName = this.$route.query['cat-name']
      if (!this.productLoadingDone) this.busy1 = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT

      if (this.$route.query.filtered) {
        const params = Object.assign({ offset, limit }, this.$route.query)
        delete params.filtered
        this.loader('/sort-and-filter', params,
          `filtered products of ${categoryName}`)
          .then(response => {
            this.productLoaded = true
            this.busy1 = false
            this.setProducts(response.data.products, limit)
          })
      } else {
        this.loader('/products/category',
          { name: categoryName, offset, limit },
          `products of ${categoryName}`)
          .then(response => {
            this.productLoaded = true
            this.busy1 = false
            this.setProducts(response.data.products, limit)
          })
      }
    },
    loadRelatedBrands () {
      const categoryName = this.$route.query['cat-name']
      const offset = this.relatedBrands.length
      const limit = 5
      this.loader(
        '/category/brands',
        { 'cat-name': categoryName, offset, limit },
        `brands of ${categoryName}`
      )
        .then(response => {
          this.relatedBrands = response.data
        })
    },
    clearRelatedBrands () {
      this.relatedBrands = []
    },
    clearSubCategories () {
      this.subCategories = []
      this.subCategoryLoadingDone = true
    },
    clearProducts () {
      this.products = []
      this.productLoadingDone = true
    }
  },
  watch: {
    '$route' (to, from) {
      if (to.query.brand && !Array.isArray(to.query.brand)) {
        to.query.brand = [to.query.brand]
      }
      if (to.query.specifications) {
        to.query.specifications = JSON.parse(to.query.specifications)
      }
      if (to.query['cat-name'] !== from.query['cat-name']) {
        this.loadCategory()
        this.clearSubCategories()
        this.loadSubCategories()
        this.clearRelatedBrands()
        this.loadRelatedBrands()
      }
      this.clearProducts()
      this.loadProducts()
    }
  },
  created () {
    if (this.$route.query.brand && !Array.isArray(this.$route.query.brand)) {
      this.$route.query.brand = [this.$route.query.brand]
    }
    if (this.$route.query.specifications) {
      this.$route.query.specifications = JSON.parse(this.$route.query.specifications)
    }
    this.loadCategory()
    this.loadSubCategories()
    this.loadProducts()
    this.loadRelatedBrands()
  }
}
</script>

<style scoped>
  #main {
    background-color: rgb(221, 221, 221);
  }
  .padding-class {
    padding: 0.5%;
  }
</style>
